.gallery {
    background-image: linear-gradient(rgba($color-primary-light, 0.7) 10%, rgba($color-primary-light, 0.3));
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color:#000;
    margin: 0;
    padding: 0;
  }

.swiper {
  &-container {
  width: 100%;
  padding-top: 2rem;
  padding-bottom: 0.5rem;
  }
  
  &-slide {
    background-position: center;
    background-size: cover;
    width: 20rem;
    height: 30rem;
    background: #fff;

    & .imgBx {
      width: 100%;
      height: 20rem;
      overflow: hidden;

      & img {
          width: 20rem;
      }
    }

    & .details {
      box-sizing: border-box;
      font-size: 20px;
      padding: 20px;

      & h3 {
        margin: 0;
        padding: 0;
        font-size: 20px;
        text-align: center;
        line-height: 20px;
        
        & span {
          font-size: 16px;
          color: #f44;
        }
      }
    }
  }
}