@keyframes zoomInBottom {
  0% {
    opacity: 0;
    transform: scaleY(0);
  }
  45% {
    opacity: 0;
  }
  55% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: scaleY(1);
  }
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

.u-margin-top-small {
  margin-top: 0.5rem !important;
}

.u-margin-top-medium {
  margin-top: 2rem !important;
}

.u-margin-top-large {
  margin-top: 8rem !important;
}

.u-margin-bottom-small {
  margin-bottom: 0.5rem !important;
}

.u-margin-bottom-large {
  margin-bottom: 4rem !important;
}

.u-margin-right-medium {
  margin-right: 2rem !important;
}

.u-margin-left-small {
  margin-left: 0.5rem !important;
}

.u-margin-left-medium {
  margin-left: 1.5rem !important;
}

.u-padding-bottom-medium {
  padding-bottom: 3rem !important;
}

.u-padding-bottom-big {
  padding-bottom: 6rem !important;
}

body {
  font-family: 'Lora', serif;
  font-weight: 500;
  font-size: 1.3rem;
  color: #000000;
}

.heading-primary {
  color: #3a2505;
  backface-visibility: hidden;
  margin-bottom: 4rem;
}

.heading-primary--main {
  display: block;
  text-transform: uppercase;
  font-weight: 100;
  font-size: 3.5rem;
  letter-spacing: 1.8rem;
}

.heading-primary--sub {
  display: block;
  font-size: 2rem;
  font-weight: 400;
  letter-spacing: 0.75rem;
}

.heading-secondary {
  font-size: 2.75rem;
  font-weight: 400;
  color: #3a2505;
  border-bottom: 1px solid;
  text-indent: 12rem;
  margin-left: -10rem;
  margin-right: 2rem;
}

.heading-tertiary {
  font-size: 1.6rem;
  color: #fffee7;
  line-height: 1.5;
  font-weight: 400;
}

.heading-tertiary--sub {
  color: #3a2505;
  font-weight: 700;
  text-indent: 1rem;
}

.paragraph {
  font-size: 1.6rem;
  font-weight: 100;
  line-height: 1.5;
  color: #6b5129;
  text-indent: 3rem;
  text-align: justify;
}

.title {
  padding: 8rem 0 0 10%;
}

.btn-nav {
  text-decoration: none;
  border: none;
  border-radius: 0 0 1rem 1rem;
  background-color: rgba(255, 254, 231, 0.65);
  font-weight: 600;
  text-transform: uppercase;
  margin: 0 1.5rem;
  padding: 1rem;
  transition: all .2s;
  color: #6b5129;
}

.btn-nav:hover {
  background-color: #fffee7;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  transform: scale(1.05);
}

.btn-nav__logo {
  height: 6.5rem;
  display: flex;
  justify-items: center;
  text-transform: none;
  align-items: center;
  margin: 1rem 1rem 0 1rem;
  padding: 4rem 3rem 3rem 3rem;
  border-radius: 0 0 1rem 1rem;
  font-family: 'Great Vibes', cursive;
  font-size: 3.5rem;
  font-weight: 400;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  z-index: 3;
  background-color: #fffee7;
  color: #6b5129;
}

.btn-nav__logo:hover {
  background-color: #fffee7;
  transform: scale(1.01);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
}

.btn-text:link, .btn-text:visited {
  font-size: 1.6rem;
  font-weight: 700;
  text-decoration: none;
  color: #fffee7;
  align-self: flex-end;
  background: linear-gradient(135deg, #6b5129, #3a2505, #3a2505);
  background-color: #3a2505;
  background-size: 180%;
  border: 1px solid rgba(58, 37, 5, 0.05);
  border-radius: 1rem;
  padding: 1rem 2rem 1.2rem 2rem;
  transition: all .5s ease;
  display: flex;
  align-items: center;
}

.btn-text:hover {
  background-position: -50px;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  transform: translateY(-3px);
}

.btn-text:active {
  box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.15);
  transform: translateY(-1px);
}

.about {
  width: 100%;
  background-image: linear-gradient(#fffee7 10%, rgba(255, 254, 231, 0.7));
  margin: auto;
  display: flex;
}

.about__description {
  width: 50%;
  flex-grow: 1;
}

.about__text-bar {
  padding: 0 6rem 0 10rem;
  margin-left: -1px;
  display: flex;
  flex-direction: column;
}

.about__right {
  flex-grow: 1;
  width: 50%;
  display: flex;
  justify-content: center;
  margin: 6.6rem 0 6.6rem 0;
}

.about__photo {
  border-radius: 1rem;
  box-shadow: -1rem 0 2rem rgba(0, 0, 0, 0.2);
  z-index: 2;
  width: 80%;
}

.gallery {
  background-image: linear-gradient(rgba(255, 254, 231, 0.7) 10%, rgba(255, 254, 231, 0.3));
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper-container {
  width: 100%;
  padding-top: 2rem;
  padding-bottom: 0.5rem;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 20rem;
  height: 30rem;
  background: #fff;
}

.swiper-slide .imgBx {
  width: 100%;
  height: 20rem;
  overflow: hidden;
}

.swiper-slide .imgBx img {
  width: 20rem;
}

.swiper-slide .details {
  box-sizing: border-box;
  font-size: 20px;
  padding: 20px;
}

.swiper-slide .details h3 {
  margin: 0;
  padding: 0;
  font-size: 20px;
  text-align: center;
  line-height: 20px;
}

.swiper-slide .details h3 span {
  font-size: 16px;
  color: #f44;
}

.features {
  width: 100%;
  background-image: linear-gradient(rgba(255, 254, 231, 0.3) 10%, rgba(255, 254, 231, 0));
  display: flex;
  padding: 2rem;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 5rem 10rem 22rem 10rem;
  z-index: 4;
}

.feature-box {
  width: 20rem;
  padding: 2rem 0 0 0;
  margin: 1.5rem;
  background-color: #6b5129;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: all ease 0.2s;
  background: linear-gradient(135deg, rgba(107, 81, 41, 0.9), #3a2505);
  border: 0.1rem solid #6b5129;
}

.feature-box__icon {
  font-size: 5rem;
  color: #fffee7;
  padding: 0 1rem;
  width: 100%;
  z-index: 6;
}

.feature-box__feature {
  z-index: 2;
  padding-top: 1rem;
}

.feature-box__hidden-text {
  width: 100%;
  transform: scaleY(0);
  color: transparent;
  transition: all ease 0.2s;
  border-radius: 0 0 1rem 1rem;
  margin-top: -10rem;
  padding: 1rem;
  opacity: 0;
}

.feature-box:hover {
  margin-bottom: -10rem;
  z-index: 6;
  transform: scale(1.05);
}

.feature-box:hover ~ .feature-box:not(:hover) {
  opacity: 0.9;
  transform: scale(0.95);
}

.feature-box:hover > .feature-box__hidden-text {
  transform: scaleY(1);
  margin-top: 0;
  color: #fffee7;
  animation-name: zoomInBottom;
  animation-duration: 0.2s;
  animation-timing-function: ease;
  z-index: 7;
  opacity: 1;
}

.feature-box:hover ~ .feature-box:last-of-type {
  opacity: 0;
}

.feature-box:last-of-type {
  opacity: 0;
}

.header {
  width: 100%;
  height: 100vh;
}

.header__background {
  height: 100%;
  width: 100%;
  background-image: url("../img/tuning/header.jpg");
  min-width: 1000px;
  background-size: cover;
  background-position-y: 30%;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  z-index: -10;
}

.header__text-box {
  position: fixed;
  top: 40%;
  text-align: center;
  width: 100%;
  height: 15%;
  z-index: -10;
  margin-bottom: 20vh;
}

.navigation {
  width: 100%;
  height: 3rem;
  z-index: 1000;
  margin: 0;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.footer {
  display: flex;
  border-radius: 1rem;
  align-items: flex-end;
  margin-top: -33rem;
  margin-bottom: -6rem;
}

.footer__left {
  flex-grow: 2;
  display: flex;
  flex-direction: column;
  height: 13rem;
  position: relative;
}

.footer__right {
  background: linear-gradient(135deg, rgba(107, 81, 41, 0.9), #3a2505);
  border-radius: 1rem 1rem 0 0;
  transition: all 0.5s ease;
  width: 33rem;
  height: 33rem;
  box-shadow: -1rem 2rem 2rem rgba(0, 0, 0, 0.3);
  z-index: 3;
}

.footer__right:hover {
  width: 43rem;
  height: 43rem;
  box-shadow: -1rem 2rem 2rem rgba(0, 0, 0, 0.4);
  margin-top: -10rem;
}

.footer__right:hover .map {
  width: 40rem;
  height: 40rem;
}

.contact-panel {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  align-items: flex-end;
  background-color: #6b5129;
  align-self: flex-end;
  border-radius: 1rem 0 0 0;
  position: relative;
}

.contact-panel__list {
  list-style: none;
}

.contact-panel__item {
  text-align: right;
}

.copyright {
  background-image: linear-gradient(to left, #6b5129 80%, #3a2505 100%);
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 0.2rem 0.5rem;
  z-index: 5;
  color: #fffee7;
  font-size: 1rem;
  font-weight: 100;
  border-radius: 1rem 0 0 1rem;
}

.borderline-vertical {
  z-index: 10;
  position: absolute;
  border-left: 1px solid;
  position: absolute;
  top: 10px;
  bottom: 0;
  right: 0;
  border-image: linear-gradient(to top, #fffee7, rgba(0, 0, 0, 0)) 1 100%;
}

.social-panel {
  display: flex;
  flex-direction: column;
  background-color: #6b5129;
  position: relative;
  height: 13rem;
  justify-self: flex-end;
  padding: 0 1.5rem;
}

.social-panel__text {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  color: #fffee7;
  margin-top: 1rem;
}

.social-panel__icons {
  display: flex;
  justify-content: center;
  flex-grow: 2;
}

.social-panel__icon {
  font-size: 4rem;
  margin: 1rem;
  color: #fffee7;
  transition: all 0.2s ease;
}

.social-panel__icon:hover {
  color: #3a2505;
}

.map {
  margin: 1.5rem;
  width: 30rem;
  height: 30rem;
  border-radius: 1rem;
  border: none;
  transition: all 0.5s ease;
}
