.header {
    width: 100%;
    height: 100vh;

    &__background{
        height: 100%;
        width: 100%;
        background-image: url("../img/tuning/header.jpg");
        min-width: 1000px;
        background-size: cover;
        background-position-y:30%;
        position: fixed;
        top:0;
        left:0;
        text-align: center;
        z-index: -10;
    }
    
    &__text-box{
        position: fixed;
        top: 40%;
        text-align: center;
        width: 100%;
        height: 15%;
        z-index: -10;
        margin-bottom: 20vh;
    }
}