.about {
    width: 100%;
    background-image: linear-gradient(rgba($color-primary-light, 1) 10%, rgba($color-primary-light, 0.7));
    margin: auto;
    display: flex;
    // box-shadow:  -1rem 0 2rem rgba($color-black, 0.2);;

    &__description {
        width: 50%;
        flex-grow: 1;
    }

    &__text-bar {
        padding: 0 6rem 0 10rem;
        margin-left: -1px;
        display: flex;
        flex-direction: column;
    }

    &__right {
        flex-grow: 1;
        width: 50%;
        display: flex;
        justify-content: center;
        margin: 6.6rem 0 6.6rem 0;
    }

    &__photo {
        border-radius: 1rem;
        box-shadow: -1rem 0 2rem rgba($color-black, 0.2);
        z-index: 2;
        width: 80%;
    }
}