body {
    font-family: 'Lora', serif;
    font-weight: 500;
    font-size: 1.3rem;
    color: $color-black;
}

.heading-primary{
    color: $color-primary-dark;
    backface-visibility: hidden;
    margin-bottom: 4rem;

    &--main{
        display: block;
        text-transform: uppercase;
        font-weight: 100;
        font-size: 3.5rem;
        letter-spacing: 1.8rem;
    }
    &--sub{
        display: block;
        font-size: 2rem;
        font-weight: 400;
        letter-spacing: 0.75rem;
    }
}

.heading-secondary{
    font-size: 2.75rem;
    font-weight: 400;
    color:  $color-primary-dark;
    border-bottom: 1px solid;
    text-indent: 12rem;
    margin-left: -10rem;
    margin-right: 2rem;
}

.heading-tertiary{
    font-size: 1.6rem;
    color: $color-primary-light;
    line-height: 1.5;
    font-weight: 400;

    &--sub {
        color:  $color-primary-dark;
        font-weight: 700;
        text-indent: 1rem;
    }

}

.paragraph{
    font-size: 1.6rem;
    font-weight: 100;
    line-height: 1.5;
    color: $color-primary;
    text-indent: 3rem;
    text-align: justify;
}

.title {
    padding: 8rem 0 0 10%;
}