.btn-nav {
    text-decoration: none;
    border: none;
    border-radius: 0 0 1rem 1rem;
    background-color: rgba($color-primary-light, 0.65);
    font-weight: 600;
    text-transform: uppercase;
    margin: 0 1.5rem;
    padding: 1rem;
    transition: all .2s;
    color: $color-primary;


    &:hover {
        background-color: rgba($color-primary-light, 1);
        box-shadow: 0 0.5rem 1rem rgba($color-black, 0.15);
        transform: scale(1.05);
    }
    
    &__logo {
        height: 6.5rem;
        display: flex;
        justify-items: center;
        text-transform: none;
        align-items: center;
        margin: 1rem 1rem 0 1rem;
        padding: 4rem 3rem 3rem 3rem;
        border-radius: 0 0 1rem 1rem;
        font-family: 'Great Vibes', cursive;
        font-size: 3.5rem;
        font-weight: 400;
        box-shadow: 0 1rem 2rem rgba(0,0,0, .1);
        z-index: 3;
        background-color: rgba($color-primary-light, 1);
        color: $color-primary;

        &:hover {
            background-color: rgba($color-primary-light, 1);
            transform: scale(1.01);
            box-shadow: 0 1rem 2rem rgba($color-black, .15);
        }
    }
}

.btn-text {
    &:link,
    &:visited {
        font-size: 1.6rem;
        font-weight: 700;
        text-decoration: none;
        color: $color-primary-light;
        align-self: flex-end;
        background: linear-gradient(135deg, $color-primary, $color-primary-dark, $color-primary-dark);
        background-color: $color-primary-dark;
        background-size: 180%;
        border: 1px solid rgba($color-primary-dark, 0.05);
        border-radius: 1rem;
        padding: 1rem 2rem 1.2rem 2rem;
        transition: all .5s ease;
        display: flex;
        align-items: center;
    }

    &:hover{
        background-position: -50px;  
        box-shadow: 0 1rem 2rem rgba($color-black, 0.15);
        transform: translateY(-3px);
    }
    
    &:active {
        box-shadow: 0 .5rem 2rem rgba($color-black, 0.15);
        transform: translateY(-1px);
    }
}