.features {
  width: 100%;
  background-image: linear-gradient(rgba($color-primary-light, 0.3) 10%, rgba($color-primary-light, 0));
  display: flex;
  padding: 2rem;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 5rem 10rem 22rem 10rem;
  z-index: 4;
  //   padding-right: 40rem;
}
.feature-box {
  width: 20rem;
  padding: 2rem 0 0 0;
  margin: 1.5rem;
  background-color: $color-primary;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: all ease 0.2s;
  background: linear-gradient(135deg, rgba($color-primary, 0.9), $color-primary-dark);
  border: 0.1rem solid $color-primary;

  &__icon {
    font-size: 5rem;
    color: $color-primary-light;
    // background-color: $color-primary;
    padding: 0 1rem;
    width: 100%;
    z-index: 6;
  }

  &__feature {
    // background-color: $color-primary;
    z-index: 2;
    padding-top: 1rem;
  }

  &__hidden-text {
    width: 100%;
    transform: scaleY(0);
    color: transparent;
    transition: all ease 0.2s;
    border-radius: 0 0 1rem 1rem;
    margin-top: -10rem;
    padding: 1rem;
    opacity: 0;
  }

  &:hover {
    margin-bottom: -10rem;
    z-index: 6;
    transform: scale(1.05);
  }

  &:hover ~ &:not(:hover) {
    opacity: 0.9;
    transform: scale(0.95);
  }

  &:hover > &__hidden-text {
    transform: scaleY(1);
    margin-top: 0;
    color: $color-primary-light;
    animation-name: zoomInBottom;
    animation-duration: 0.2s;
    animation-timing-function: ease;
    z-index: 7;
    opacity: 1;
  }

  &:hover ~ &:last-of-type {
    opacity: 0;
  }

  &:last-of-type {
    opacity: 0;
  }
}
