.footer {
    display: flex;
    border-radius: 1rem;
    align-items: flex-end;
    margin-top: -33rem;
    margin-bottom: -6rem;

    &__left {
        flex-grow: 2;
        display: flex;
        flex-direction: column;
        height: 13rem;
        position: relative;
    }

    &__right {
        background: linear-gradient(135deg, rgba($color-primary, 0.9), $color-primary-dark);
        border-radius: 1rem 1rem 0 0;
        transition: all 0.5s ease;
        width: 33rem;
        height: 33rem;
        box-shadow: -1rem 2rem 2rem rgba($color-black, 0.3);
        z-index: 3;

        &:hover {
            width: 43rem;
            height: 43rem;
            box-shadow: -1rem 2rem 2rem rgba($color-black, 0.4);
            margin-top: -10rem;
        }

        &:hover .map {
            width: 40rem;
            height: 40rem;
        }
    }
}

.contact-panel {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 1rem 2rem;
    align-items: flex-end;
    background-color: $color-primary;
    align-self: flex-end;
    border-radius: 1rem 0 0 0;
    position: relative;

    &__list {
    list-style: none;
    }

    &__item {
        text-align: right;
    }

}

.copyright {
    background-image: linear-gradient(to left, $color-primary 80%, $color-primary-dark 100%);
    position: absolute;
    right: 0;
    bottom: 0;
    padding: 0.2rem 0.5rem;
    z-index: 5;
    color: $color-primary-light;
    font-size: 1rem;
    font-weight: 100;
    border-radius: 1rem 0 0 1rem;
}

.borderline-vertical {
    z-index:10;
    position: absolute;
    border-left: 1px solid;
    position: absolute;
    top: 10px;
    bottom: 0;
    right: 0;
    border-image: linear-gradient(to top, $color-primary-light, rgba(0, 0, 0, 0)) 1 100%;
}

.social-panel {
    display: flex;
    flex-direction: column;
    background-color: $color-primary;
    position: relative;
    height: 13rem;
    justify-self: flex-end;
    padding: 0 1.5rem;

    &__text {
        flex-grow: 1;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        color: $color-primary-light;
        margin-top: 1rem;
    }

    &__icons {
        display: flex;
        justify-content: center;
        flex-grow: 2;
    }

    &__icon {
        font-size: 4rem;
        margin: 1rem;
        color: $color-primary-light;
        transition: all 0.2s ease;

        &:hover {
            color: $color-primary-dark;
        }
    }
}

.map {
    margin: 1.5rem;
    width: 30rem;
    height: 30rem;
    border-radius: 1rem;
    border: none;
    transition: all 0.5s ease;
}